.search-bar-container {
  display: flex;
  justify-content: center;
  border: 1px solid;
  padding: 1rem 0.4rem;
  background-color: #2e324e;
  border: 1px solid rgb(173, 162, 162);
  border-radius: 0.5rem;
  max-width: 480px;
  margin: 0.3rem auto;
}
#date-input {
  background-color: #3a3e59;
  border: none;
  border-radius: 0.5rem;
  padding: 0;
  cursor: pointer;
  font-size: 0.8rem;
  color: white;
}
.date-search {
  display: flex;
  justify-content: center;
  padding: 0.3rem 0;
}
.memoryScore-search {
  /* background-color: white; */
  padding: 0.3rem 0.2rem;
  margin: 0 0.3rem;
}
.memoryScore-search select {
  background-color: #3a3e59 !important;
  color: white;
}
.byWord-search {
  /* background-color: white; */
  padding: 0.3rem 0;
}
.byWord-search label {
  color: white;
}
.byWord-search input {
  padding: 0.1rem 0.1rem;
  font-size: 1.2rem;
  border: 0.5px solid white;
  border-radius: 0.5rem;
  background-color: #3a3e59 !important;
  color: white;
}
.combined-container {
  display: flex;
  justify-content: center;
}
