.login-form-container {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  display: grid;
  justify-content: center;
  background-color: rgb(105, 43, 105);
  color: white;
}
.login-form {
  display: grid;
  justify-content: center;
  align-items: center;
}

.email-input,
#passinput {
  padding: 0.5rem;
  font-size: 1rem;
  background-color: white !important;
}
.login-form-container .submit-btn {
  padding: 0.4rem 0.5rem;
  cursor: pointer;
  margin: 0.5rem auto;

  width: 50%;
}

.login-title {
  padding: 0.2rem 0.5rem;
  background-color: rgb(90, 34, 90);
}
.login-title-text {
  font-size: 1.1rem;
  text-align: center;
  padding: 0.1rem 0.2rem;
  background-color: rgb(78, 29, 78);
}
