*,
*::after,
*::before {
  /* border: 1px solid; */
}

.vocub-list-container {
  padding-top: 0.4rem;
  font-family: Helvetica, sans-serif;
}
.vocub-card {
  border: 0.8px solid rgb(187, 184, 184);
  border-radius: 0.3rem;
  max-width: 480px;
  padding: 0.2rem 0.4rem;
  margin: 0.6rem 0;
  background-color: var(--light-black);
  color: #ed6b5b;
}
.vocub-card h3 {
  margin: 0.2rem 0;
  text-align: center;
  font-size: 1.1rem;
  padding: 0.1rem 0;
  /* color: white; */
  letter-spacing: 0.025rem;
  border-bottom: 1px solid;
}
.vocub-card h5 {
  margin: 0.6rem 0;
  /* color: white; */
  word-spacing: 0.2rem;
  letter-spacing: 0.03rem;
  font-size: 1rem;
  font-weight: 200;
  word-wrap: normal;
  overflow-wrap: break-word;
}
.vocub-card p {
  /* color: white; */
  font-style: italic;
  word-spacing: 0.2rem;
  letter-spacing: 0.02rem;
  overflow-wrap: break-word;
  margin: 0.4rem 0;
  font-size: 0.93rem;
}
.vocub-card .action {
  border-top: 0.3px solid whitesmoke;
  display: flex;
  justify-content: center;
}
.vocub-card .action button {
  padding: 0.25rem 0.4rem;
  margin: 0.3rem 0.3rem;
  cursor: pointer;
  background-color: var(--light-black);
  color: var(--blue);
  border-radius: 0.5rem;
  border-color: var(--light-black);
}
.vocub-card .action button:hover {
  color: white;
  border-color: white;
}
ul {
  margin: 0;
  max-width: fit-content;
  padding: 0;
}

.vocub-list {
  margin: 0 auto;
}
