.admin-board {
  background-color: #2e3353;
  color: white;
  padding: 0.8rem 0.5rem;
  user-select: none;
}
.vocub-count-text {
  text-align: center;
  padding: 0.1rem 0.2rem;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: large;
}
.account-email {
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* border: 1px solid black; */
}
.account-email-text {
  text-align: center;
  font-size: 0.9rem;
  /* border: 1px solid black; */
}
.logout-button-div {
  display: grid;
  align-items: center;
  justify-content: center;
  padding: 0.2rem 0.5rem;
  background-color: rgb(10, 9, 9);
  width: 5rem;
  height: 1.9rem;
  margin: 0.1rem;
  cursor: pointer;
  border-radius: 0.2rem;
}
.logout-btn {
  text-align: center;
  font-size: 0.9rem;
  padding: 0.1rem;
  color: rgb(74, 167, 248);
}
.logout-btn:hover {
  color: rgb(189, 182, 182);
}
.vocub-count {
  padding: 0.5rem 0.1rem;
}

.pie-chart-container {
  display: flex;
  justify-content: center;
  padding: 0.5rem 0;
}
#drawing-canvas {
  background-color: white;
}
